@import "@webx/utils/optional.scss";

@import "./header.scss";
@import "./x-block.scss";
@import "./x-button.scss";
@import "./x-check-box.scss";
@import "./x-dropdown.scss";
@import "./x-text-field.scss";
@import "./x-menu.scss";
@import "./x-slider.scss";
@import "./x-tumbler.scss";
@import "./html.scss";


@mixin theme-dark {
    background: #000000;
    color-scheme: dark;
    color: #ffffff;

    .webx {
        font-family: 'TT Commons';
        @include html;
        @include header;
        @include x-block;
        @include x-button;
        @include x-check-box;
        @include x-dropdown;
        @include x-text-field;
        @include x-menu;
        @include x-slider;
        @include x-tumbler;
    }
}